import { loadData } from '@/api/common'
import ModalBg from '@/components/base/modal-bg'
export default {
  components: {
    ModalBg
  },
  data() {
    return {
      isShow: true,
      giftExchange: true,
      modalShow: false,
      addressSelect: [],
      contactName: '',
      contactPhone: '',
      addressDetail: '',
      addressId: ''
    }
  },
  props: {
    clickBgClose: {
      type: Boolean,
      default: false
    },
    convert: {
      type: Object
    }
  },
  created() {
    this.initDate()
  },
  methods: {
    initDate() {
      loadData('member/address/list').then(data => {
        console.log(data)
        this.addressSelect = data.data
      })
    },
    next(contactName, contactPhone, addressDetail, addressId) {
      this.isShow = false
      sessionStorage.setItem('contactName', contactName)
      sessionStorage.setItem('contactPhone', contactPhone)
      sessionStorage.setItem('addressDetail', addressDetail)
      sessionStorage.setItem('addressId', addressId)

      this.contactName = sessionStorage.getItem('contactName')
      this.contactPhone = sessionStorage.getItem('contactPhone')
      this.addressDetail = sessionStorage.getItem('addressDetail')
      this.addressId = sessionStorage.getItem('addressId')
    },
    affirm() {
      loadData('/member/gift/exchange', {
        addressId: this.addressId,
        id: this.convert.id
      }).then(data => {
        this.isShow = false
        this.modalShow = false
        this.giftExchange = false
      })
    },
    cancel() {
      this.modalShow = false
      this.isShow = true
    },
    closeModal() {
      if (!this.clickBgClose) {
        return false
      }
      this.modalShow = false
    },
    show() {
      this.modalShow = true
    },
    hide() {
      this.modalShow = false
    }
  }
}



